<template>
	<view class="container">
		<view style="display: flex;flex-direction: row;width: 100%;margin-top: 80rpx;align-items: center;" class="header">
			<view style="width: 80%;flex-shrink: 0;margin-left: 5%;">
				<uni-search-bar v-model="search_value"  radius="100" placeholder="歌曲名" clearButton="none" cancelButton="none" @confirm="search" />
			</view>
			<image src="@/static/scan.png" style="width: 7%;" mode="widthFix"></image>
		</view>
		  
		<view style="width: 90%;margin-left: 5%;">
			<uni-swiper-dot :info="info" :current="swiper_current" field="content" mode="default">
				<swiper  @change="change_swiper">
					<swiper-item v-for="(item ,index) in info" :key="index">
						<image :src="item.image" style="width: 100%;" mode="widthFix"></image>
					</swiper-item>
				</swiper>
			</uni-swiper-dot>
			
		</view>
		
		<view style="display: flex;flex-direction: row;width: 100%;justify-content: space-around;margin-top: 80rpx;" >
			<view style="display: flex;flex-direction: column;width: 20%;flex-shrink: 0;align-items: center;" @click="jump_shichang()">
				<image src="../../../static/navgiation/ShouYe/ShiChang.png" style="width: 50%;" mode="widthFix"></image>
				<text>视唱训练</text>
			</view>
			<view style="display: flex;flex-direction: column;width: 20%;flex-shrink: 0;align-items: center;" @click="jump_tingyin()">
				<image src="../../../static/navgiation/ShouYe/TingYin.png" style="width: 50%;" mode="widthFix"></image>
				<text>听音训练</text>
			</view>
			<view style="display: flex;flex-direction: column;width: 20%;flex-shrink: 0;align-items: center;" @click="jump_personalise()">
				<image src="../../../static/navgiation/ShouYe/TuiJian.png" style="width: 50%;" mode="widthFix"></image>
				<text>个性推荐</text>
			</view>
		</view>
		
		<view style="display: flex;flex-direction: column;background-color: #f7f7f7;">
			<uni-section title="视频教学" type="line"></uni-section>
			<view style="display: flex;flex-direction: row;flex-wrap: wrap;background-color: #f7f7f7;">
				<view @click="jump_video(index)" v-for="(item ,index) in video" style="background-color: #f7f7f7;width: 48%;flex-shrink: 0;margin-left: 1%;display: flex;flex-direction: column;background-color: #ffffff;margin-top: 10rpx;border-radius: 20upx;">
					
					<view :style="{backgroundImage: 'url('+item.href+')'}" class="scroll-view-img1">
						<image src="../../../static/navgiation/ShouYe/bofang.png" style="width: 20%;margin-left: 40%;" mode="widthFix"></image>
					</view>
					<view style="">
						<text style="color: #eb90aa;border: 1px solid #eb90aa;font-size: 12px;border-radius: 10upx;">自创</text>
						<text style="font-size: 12px;">{{item.title.substring(0,26)}}</text>
					</view>
					
				</view>
			</view>
			<uni-section title="文本教学" type="line"></uni-section>
			<view style="display: flex;flex-direction: column;font-family: xingkai;">
				
				
				<view v-for="(item,index) in info" style="width: 88%;margin-left: 6%;margin-top: 40rpx;text-align: center;">
					<uni-dateformat :date="item.time"></uni-dateformat>
					<image :src="item.image" style="width: 100%;margin-top: 20rpx;" mode="widthFix"></image>
					<view style="background-color: #ffffff;margin-top: -2px;text-align: left;min-height: 100rpx;padding: 10rpx">
						<text style="font-size: 12px;">{{item.title}}</text>
					</view>
				</view>
			</view>
		</view>
<!-- 		<wrap-version-update id="6302285884b51a00019ea12f" @check="handleCheck" @error="handleError"
		            @finish="handleFinish">
		</wrap-version-update> -->
	</view>
</template>

<script>
	export default { 
		data() {
			return {
				search_value:'',
				info: [{
					image: 'https://7463-tcb-6dkhphdodd5e2a-7dedu72135999-1309304321.tcb.qcloud.la/ShiYin/swiper1.png',
					title:'【800集钢琴教程】包含所有钢琴技巧，想学不会都难，建议收藏，学完变',
					time:1658745213000
				}, {
					image: 'https://7463-tcb-6dkhphdodd5e2a-7dedu72135999-1309304321.tcb.qcloud.la/ShiYin/swiper2.png',
					title:'[钢琴教学] 你的心河 River flows in you－李闰珉 Yiruma [慢速版]',
					time:1658748213000
				}, {
					image: 'https://7463-tcb-6dkhphdodd5e2a-7dedu72135999-1309304321.tcb.qcloud.la/ShiYin/swiper3.png',
					title:'《菊次郎的夏天》钢琴教学',
					time:1658745243000
				},{ 
					image: 'https://7463-tcb-6dkhphdodd5e2a-7dedu72135999-1309304321.tcb.qcloud.la/ShiYin/swiper4.png',
					title:'周杰伦《反方向的钟》钢琴教学！太简单了吧',
					time:1658746213000
				}],
				swiper_current: 0,
				video:''
			}
		},
		mounted() {
			this.login_auto();
			

		},
		methods: {
			jump_personalise(){
				uni.navigateTo({
					url:'/pages/xunlian/personalise/personalise'
				})
			},
			jump_shichang(){

				uni.navigateTo({
					url:'/pages/xunlian/shichang/list/list'
				})
			},
			jump_tingyin(){
				uni.navigateTo({
					url:'/pages/xunlian/tingyin/tingyin'
				})
			},
			jump_video(index){
				uni.setStorage({
					data:this.video[index],
					key:'video_chuancan'
				});
				uni.setStorage({
					data:this.video,
					key:'all_video'
				});
				uni.navigateTo({
					url:'video/video'
				})
			},
			login_auto(){
				uni.request({
					url:this.server_url+'/loginByauto?token='+uni.getStorageSync('token'),
					method:'GET',
					success: (res) => {
						console.log(res)
						if(res.data==false){
							uni.redirectTo({
								url:'../../login/login'
							});
							uni.showToast({
								title:'请先登录',
								icon:'none'
							});   
							return;
						}
						uni.request({
							url:this.server_url+'/getvideo',
							method:'GET',
							success: (res) => {
								// console.log(res)
								this.video=res.data;
							}
						}) 
					}
				})
			},
			search() {
				console.log('search');
			},
			change_swiper(e) {
				this.swiper_current = e.detail.current;
			},
			handleFinish(res){
				console.log(res)
			},
			handleError(res){
				console.log(res)
			},
			handleCheck(res){
				console.log(res)
			},
		}
	}
</script>

<style>
	.continer{
		width: 90%;
		display: flex;
		min-height: 100vh;
		flex-direction: column;
		margin-left: 5%;
		font-family: xingkai;
	}
	page {
		width: 100%;
		height: 100%;
	}
	.scroll-view-img1{
		height: 250rpx;
		background-size: 100% 100%;
		border-radius: 25upx;
		margin-top: 30rpx;
		width: 100%;
		display: flex;
		flex-direction: row;
		align-items: center;
		
	}
</style>
